import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from 'views/home/Home.vue'
import store from '../store/index'

Vue.use(VueRouter)

const routes = [{
  path: '/home',
  redirect: '/'
}, {
  path: '/',
  name: 'Home',
  component: Home
}, {
  path: '/login',
  name: 'Login',
  component: () => import('views/login/Login.vue'),
  children: [{
      path: '/login/',
      component: () => import('views/login/children/LoginBox.vue')
    },
    {
      path: '/login/register',
      component: () => import('views/login/children/RegisterBox.vue')
    }
  ]
}];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

router.beforeResolve((to, from, next) => {
  let sno = store.state.user.sno;
  if (to.path != '/login' && to.path != '/login/register') {
    if (sno != null) {
      next();
    } else {
      next('/login');
    }
  } else {
    next();
  }
});

export default router;



