import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: {
      sno: null
    },
  },
  mutations: {
    setSno(state, sno) {
      state.user.sno = sno;
    },
    removeSno(state) {
      state.user.sno = null;
    }
  },
  modules: {}
});



