<template>
  <div class="homeUserInfo">
    <div class="baffles"></div>
    <div class="userInfoMain">
      <div class="username">
        <span v-if="type == 'show'" class="content">{{user.username}}</span>
        <input v-else type="text" class="content" v-model="user.username" />
      </div>
      <div :class="sexClass" @click="changeSex"></div>
      <div class="other">
        <div class="sno">
          学号：
          <span class="content">{{user.sno}}</span>
        </div>
        <div class="age">
          年龄：
          <span v-if="type == 'show'" class="content">{{user.age}}</span>
          <input v-else type="text" class="content" v-model="user.age" />
        </div>
        <div class="height">
          身高：
          <span v-if="type == 'show'" class="content">{{user.height}}</span>
          <input v-else type="text" class="content" v-model="user.height" />
        </div>
        <div class="weight">
          体重：
          <span v-if="type == 'show'" class="content">{{user.weight}}</span>
          <input v-else type="text" class="content" v-model="user.weight" />
        </div>
      </div>
      <div class="personalProfile">
        <div class="title">个人简介</div>
        <div v-if="type == 'show'" class="content">{{user.personalProfile}}</div>
        <textarea v-else class="content" v-model="user.personalProfile"></textarea>
      </div>
      <div class="contactInformation">
        <div class="title">联系方式</div>
        <div v-if="type == 'show'" class="content">{{user.contactInformation}}</div>
        <textarea v-else class="content" v-model="user.contactInformation"></textarea>
      </div>
      <button @click="commit">确定</button>
    </div>
  </div>
</template>

<script>
import { getUserInfo } from 'network/Home';

export default {
  name: 'HomeUserInfo',
  data () {
    return {
      user: {},
    };
  },
  props: ['sno', 'type'],
  computed: {
    sexClass () {
      let arr = ['sex'];
      if (this.type == 'edit') {
        arr.push('edit');
      }
      if (this.user.sex == '男') {
        arr.push('sexm');
      } else if (this.user.sex == '女') {
        arr.push('sexf');
      }
      return arr;
    }
  },
  methods: {
    changeSex () {
      if (this.type == 'show') {
        return;
      }
      if (this.user.sex == '男') {
        this.user.sex = '女';
      } else if (this.user.sex == '女') {
        this.user.sex = '?';
      } else {
        this.user.sex = '男';
      }
    },
    commit () {
      this.$emit('submit', this.user);
    }
  },
  created () {
    getUserInfo({
      sno: this.sno,
      type: 'complete'
    }).then(res => {
      this.user = res.data;
      this.user.sno = this.sno;
    }).catch(err => {
      console.log(err);
    });
  }
}
</script>

<style>
.homeUserInfo > .baffles {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.homeUserInfo > .userInfoMain {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  width: 360px;
  height: 420px;
  padding: 10px 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px #fff;
  box-sizing: border-box;
  background-color: #fff;
  transform: translate(-50%, -50%);
}

.homeUserInfo > .userInfoMain > .username {
  font-size: 32px;
  color: #333;
}

.homeUserInfo > .userInfoMain > .username > .content {
  display: block;
  width: 100%;
  border: 0px;
  font-size: 32px;
  font-family: "Microsoft YaHei";
  color: #333;
  background-color: transparent;
}

.homeUserInfo > .userInfoMain > .sex {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
  background-image: url("~assets/img/sex.png");
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center center;
}

.homeUserInfo > .userInfoMain > .sexm {
  background-image: url("~assets/img/sexm.png");
}

.homeUserInfo > .userInfoMain > .sexf {
  background-image: url("~assets/img/sexf.png");
}

.homeUserInfo > .userInfoMain > .edit {
  cursor: pointer;
}

.homeUserInfo > .userInfoMain > .other {
  display: flex;
  height: 30px;
  margin: 10px 0px 0px;
  font-size: 12px;
  color: #666;
}

.homeUserInfo > .userInfoMain > .other > .sno {
  flex: 1;
  font-size: 12px;
  color: #666;
}

.homeUserInfo > .userInfoMain > .other > .sno > .content {
  display: inline-block;
  width: 80px;
  border: 0px;
  margin-left: -5px;
  font-size: 12px;
  font-family: "Microsoft YaHei";
  color: #666;
  background-color: transparent;
}

.homeUserInfo > .userInfoMain > .other > .age,
.homeUserInfo > .userInfoMain > .other > .height,
.homeUserInfo > .userInfoMain > .other > .weight {
  margin-left: 5px;
}

.homeUserInfo > .userInfoMain > .other > .age > .content,
.homeUserInfo > .userInfoMain > .other > .height > .content,
.homeUserInfo > .userInfoMain > .other > .weight > .content {
  display: inline-block;
  width: 30px;
  border: 0px;
  margin-left: -5px;
  font-size: 12px;
  font-family: "Microsoft YaHei";
  color: #666;
  background-color: transparent;
}

.homeUserInfo > .userInfoMain > .other > .age > .content {
  width: 18px;
}

.homeUserInfo > .userInfoMain > .personalProfile {
  margin-bottom: 10px;
}

.homeUserInfo > .userInfoMain > .personalProfile > .title,
.homeUserInfo > .userInfoMain > .contactInformation > .title {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 700;
}

.homeUserInfo > .userInfoMain > .personalProfile > .content,
.homeUserInfo > .userInfoMain > .contactInformation > .content {
  display: block;
  width: 100%;
  height: 100px;
  padding: 10px;
  border-radius: 10px;
  box-sizing: border-box;
  font-size: 14px;
  font-family: "Microsoft YaHei";
  resize: none;
  outline: none;
}

.homeUserInfo > .userInfoMain > .personalProfile > .content::-webkit-scrollbar,
.homeUserInfo
  > .userInfoMain
  > .contactInformation
  > .content::-webkit-scrollbar {
  width: 6px;
}

.homeUserInfo
  > .userInfoMain
  > .personalProfile
  > .content::-webkit-scrollbar-thumb,
.homeUserInfo
  > .userInfoMain
  > .contactInformation
  > .content::-webkit-scrollbar-thumb {
  background-color: #999;
  border-radius: 10px;
}

.homeUserInfo
  > .userInfoMain
  > .personalProfile
  > .content:hover::-webkit-scrollbar-thumb,
.homeUserInfo
  > .userInfoMain
  > .contactInformation
  > .content:hover::-webkit-scrollbar-thumb {
  background-color: #666;
}

.homeUserInfo > .userInfoMain > .personalProfile > .content {
  border: 2px solid pink;
}

.homeUserInfo > .userInfoMain > .contactInformation > .content {
  border: 2px solid skyblue;
}

.homeUserInfo > .userInfoMain > button {
  position: absolute;
  bottom: 20px;
  left: 50%;
  padding: 5px 20px;
  border: 0px;
  border-radius: 5px;
  color: #fff;
  background-color: #3fc079;
  transform: translate(-50%, 0);
  transition: background-color 0.3s;
  cursor: pointer;
}

.homeUserInfo > .userInfoMain > button:hover {
  background-color: #439b6a;
}
</style>


