import {
  request
} from './request';

/**
 * 获取用户信息
 * @param {*} params sno, type['simple', 'complete']
 */
export function getUserInfo(params) {
  return request({
    method: 'GET',
    url: '/userInformation',
    params
  });
}

/**
 * 设置用户信息
 * @param {*} params sno, age, height, weight, personalProfile, contactInformation
 * @returns 
 */
export function setUserInfo(params) {
  return request({
    method: 'POST',
    url: '/userInformation',
    params
  });
}

/**
 * 获取配对列表 
 * @param {*} params (sno)
 */
export function getPairingList(params) {
  return request({
    method: 'GET',
    url: '/pairingList',
    params
  });
}

/**
 * 添加配对请求
 * @param {*} params sno, content
 */
export function addPairingRequest(params) {
  return request({
    method: 'POST',
    url: '/pairingList',
    params
  });
}

/**
 * 获取好友列表
 * @param {*} params sno
 */
export function getFriendList(params) {
  return request({
    method: 'GET',
    url: '/friendList',
    params
  })
}

/**
 * 查询符合条件的用户
 * @param {*} params message
 */
export function getUserList(params) {
  return request({
    method: 'GET',
    url: '/userList',
    params
  })
}

/**
 * 发起好友请求
 * @param {*} parmas applicant请求人学号 requested被请求人学号
 */
export function addFriendRequest(params) {
  return request({
    method: 'POST',
    url: '/friendRequest',
    params
  });
}

/**
 * 删除好友
 * @param {*} params reqSno, resSno
 */
export function removeFriend(params) {
  return request({
    method: 'DELETE',
    url: '/friendList',
    params
  });
}

/**
 * 获取通知信息 
 * @param {*} params sno
 */
export function getInformation(params) {
  return request({
    method: 'GET',
    url: '/information',
    params
  });
}

/**
 * 删除通知信息 
 * @param {*} params id, type
 */
export function removeInformation(params) {
  return request({
    method: 'DELETE',
    url: '/information',
    params
  });
}

/**
 * 相应好友请求
 * @param {*} params id, status
 */
export function acceptFriendRequest(params) {
  return request({
    method: 'GET',
    url: '/friendRequest',
    params
  });
}



