<template>
  <div class="header">
    <div class="logo" @click="logoClick"></div>
  </div>
</template>

<script>
export default {
  name: 'Header',
  methods: {
    logoClick () {
      this.$router.push('/home');
    }
  }
}
</script>

<style>
.header {
  position: absolute;
  top: 0px;
  z-index: 100;
  width: 100%;
  height: 68px;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(1px);
}

.header > .logo {
  width: 120px;
  height: 60px;
  margin-top: 4px;
  background-image: url("~assets/img/logo.png");
  background-size: 100%;
  background-repeat: no-repeat;
  cursor: pointer;
}
</style>


