<template>
  <div class="home">
    <!-- <Header></Header> -->
    <div class="titleBox updown">
      <div class="left">
        <home-title @updateFriendList="updateFriendList"></home-title>
      </div>
      <div class="right">
        <home-user></home-user>
      </div>
    </div>
    <div class="content updown">
      <div class="left">
        <home-pairing-list></home-pairing-list>
      </div>
      <div class="right">
        <home-friend-list ref="friendList"></home-friend-list>
      </div>
    </div>
  </div>
</template>

<script>
import Header from 'components/common/Header.vue'
import HomeTitle from './children/HomeTitle.vue';
import HomeUser from './children/HomeUser.vue';
import HomePairingList from './children/HomePairingList.vue';
import HomeFriendList from './children/HomeFriendList.vue';
export default {
  name: 'Home',
  components: {
    Header,
    HomeTitle,
    HomeUser,
    HomePairingList,
    HomeFriendList
  },
  methods: {
    updateFriendList () {
      this.$refs.friendList.updateFriendListEvent();
    }
  }
};
</script>

<style>
.home {
  width: 100vw;
  height: 100vh;
  background-image: url("~assets/img/bg1.jpg");
  background-size: cover;
  background-position: 0px, -10px;
  overflow: hidden;
}

.home > .titleBox {
  margin: 40px auto 0px;
  width: 1080px;
  height: 80px;
}

.home > .content {
  margin: 40px auto;
  width: 1080px;
  height: 500px;
}

.updown > div {
  box-shadow: 0px 8px 30px -15px #000;
}

.updown > .left {
  float: left;
  width: 800px;
  height: 100%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}

.updown > .right {
  float: right;
  width: 240px;
  height: 100%;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.8);
}
</style>


