</template>

<script>
import tool from 'common/utils.js';
import store from './store/index';
window.tool = tool;

let sno = tool.getCookie('sno');
if (sno) {
  store.commit('setSno', sno);
}

export default {
  name: 'App',
  methods: {
  },
  created () {
    // let sno = tool.getCookie('sno');
    // if (sno) {
    //   this.$store.commit('setSno', sno);
    // }
  }
}
</script>

<style>
@import url("assets/css/base.css");
</style>



