<template>
  <div class="homeTips">
    <div class="baffles"></div>
    <div class="homeTipsMain">
      <div class="content">{{content}}</div>
      <button class="accept" @click="acceptClick">{{acceptText}}</button>
      <button class="refuse" @click="refuseClick">{{refuseText}}</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeTips',
  data () {
    return {
      content: '是否确定',
      acceptText: '是',
      refuseText: '否',
    };
  },
  props: ['data'],
  methods: {
    acceptClick () {
      this.$emit('accept');
    },
    refuseClick () {
      this.$emit('refuse');
    }
  },
  mounted () {
    if (this.data.content != undefined) {
      this.content = this.data.content;
    }
    if (this.data.acceptText != undefined) {
      this.acceptText = this.data.acceptText;
    }
    if (this.data.refuseText != undefined) {
      this.refuseText = this.data.refuseText;
    }
  }
}
</script>

<style>
.homeTips > .baffles {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.homeTips > .homeTipsMain {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  width: 320px;
  height: 120px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px #fff;
  box-sizing: border-box;
  background-color: #fff;
  transform: translate(-50%, -50%);
}

.homeTips > .homeTipsMain > .content {
  font-size: 20px;
  text-align: center;
}

.homeTips > .homeTipsMain > button {
  position: absolute;
  bottom: 20px;
  padding: 5px 20px;
  border: 0px;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s;
}

.homeTips > .homeTipsMain > .accept {
  left: 80px;
  background-color: #3fc079;
}

.homeTips > .homeTipsMain > .accept:hover {
  background-color: #439b6a;
}

.homeTips > .homeTipsMain > .refuse {
  right: 80px;
  background-color: #ff8656;
}

.homeTips > .homeTipsMain > .refuse:hover {
  background-color: #f05418;
}
</style>


