<template>
  <div class="homeUser">
    <div class="userInformation">
      <div class="username">{{username}}</div>
      <div class="sno">学号：{{sno}}</div>
      <div :class="sexClass"></div>
      <div class="edit" @click="userInfoStatus = true"></div>
    </div>
    <home-user-info v-if="userInfoStatus" @submit="submit" :sno="sno" :type="'edit'" :submit="null"></home-user-info>
    <div class="logout" @click="logout">切换账号</div>
  </div>
</template>

<script>

import { getUserInfo, setUserInfo } from 'network/Home.js';
import HomeUserInfo from './HomeUserInfo.vue';

export default {
  components: {
    HomeUserInfo
  },
  name: 'HomeUser',
  data () {
    return {
      username: null,
      sno: null,
      sex: null,
      userInfoStatus: false
    };
  },
  computed: {
    sexClass () {
      let arr = ['sex'];
      if (this.sex == '男') {
        arr.push('sexm');
      } else if (this.sex == '女') {
        arr.push('sexf');
      }
      return arr;
    }
  },
  methods: {
    logout () {
      this.$router.push('/login');
    },
    submit (data) {
      this.userInfoStatus = false;
      setUserInfo({
        sno: this.sno,
        username: data.username,
        sex: data.sex,
        age: data.age,
        height: data.height,
        weight: data.weight,
        personalProfile: data.personalProfile,
        contactInformation: data.contactInformation
      }).then(res => {
        if (!res.data.status) {
          alert('修改失败');
        }
        this.sex = data.sex;
        this.username = data.username;
      }).catch(err => {
        console.log(err);
      })
      console.log(data);
    }
  },
  created () {
    getUserInfo({
      sno: this.$store.state.user.sno,
      type: 'simple'
    }).then(res => {
      let data = res.data;
      if (data.status) {
        this.sno = this.$store.state.user.sno;
        this.username = data.username;
        this.sex = data.sex;
      } else {
        alert('账号异常！');
        this.$router.push('/login');
      }
    }).catch(err => {
      console.log(err);
    });
  }
}
</script>

<style>
.homeUser {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.userInformation {
  position: relative;
  padding-left: 10px;
  width: 200px;
  height: 100%;
}

.userInformation > .username {
  font-size: 24px;
}

.userInformation > .sno {
  margin-top: 8px;
  font-size: 12px;
  color: #333;
}

.userInformation > .sex {
  position: absolute;
  top: 3px;
  right: 40px;
  width: 30px;
  height: 30px;
  background-image: url("~assets/img/sex.png");
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center center;
  cursor: default;
}

.userInformation > .sexm {
  background-image: url("~assets/img/sexm.png");
  background-size: 100%;
}

.userInformation > .sexf {
  background-image: url("~assets/img/sexf.png");
  background-size: 100%;
}

.userInformation > .edit {
  position: absolute;
  top: 3px;
  right: 0px;
  width: 30px;
  height: 30px;
  background-image: url("~assets/img/edit.png");
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
}

.homeUser > .logout {
  position: absolute;
  bottom: 15px;
  right: 20px;
  font-size: 12px;
  color: #666;
  cursor: pointer;
  transition: color 0.3s;
}

.homeUser > .logout:hover {
  color: #000;
}
</style>


