<template>
  <div class="homeTitle">
    <div class="logo"></div>
    <div class="seizeASeat"></div>
    <div class="search">
      <input type="text" />
      <button class="submit"></button>
    </div>
    <div class="information" @click="infoListStatus = true">
      <div v-if="infoList.length != 0" class="infoExist"></div>
    </div>
    <div v-if="infoListStatus" class="baffles"></div>
    <div v-if="infoListStatus" class="informationBox">
      <div class="title">消息列表</div>
      <div class="empty" v-if="infoList.length == 0">暂无消息</div>
      <ul v-else>
        <li v-for="info in infoList">
          <div class="content">{{info.content}}</div>
          <button class="accept" @click="infoSubmit(info, true)">{{info.acceptText}}</button>
          <button
            v-if="info.type == 0"
            class="refuse"
            @click="infoSubmit(info, false)"
          >{{info.refuseText}}</button>
        </li>
      </ul>
      <button @click="infoListStatus = false">返回</button>
    </div>
  </div>
</template>

<script>
import { getInformation, acceptFriendRequest, removeInformation } from 'network/Home';

export default {
  name: 'HomeTitle',
  data () {
    return {
      infoList: [],
      infoListStatus: false,
      nowInfo: {},
    };
  },
  methods: {
    infoSubmit (info, status) {
      if (info.type == 0) {
        acceptFriendRequest({
          id: info.id,
          status
        }).then(res => {
          if (res.data.status) {
            this.removeInfo(info);
            this.$emit('updateFriendList');
            if (status) {
              alert('已接受');
            } else {
              alert('已拒绝');
            }
          } else {
            alert('服务器正忙');
          }
        }).catch(err => {
          console.log(err);
        });
        if (info.pid != undefined) {
          removeInformation({
            type: 'PairingRequest',
            id: info.pid
          }).then(res => {
            if (!res.status) {
              alert('服务器正忙');
            }
          }).catch(err => {
            console.log(err);
          });
        }
      } else if (info.type == 1) {
        removeInformation({
          type: 'FriendRequest',
          id: info.id
        }).then(res => {
          if (!res.status) {
            alert('服务器正忙');
          } else {
            this.removeInfo(info);
          }
        }).catch(err => {
          console.log(err);
        });
      }
    },
    removeInfo (info) {
      let arr = [];
      for (let i = 0; i < this.infoList.length; i++) {
        if (this.infoList[i] == info) {
          continue;
        }
        arr.push(this.infoList[i]);
      }
      this.infoList = arr;
    }
  },
  created () {
    getInformation({
      sno: this.$store.state.user.sno
    }).then(res => {
      let arr = [];
      for (let info of res.data.infoList) {
        if (info.type == 1) {
          arr.push({
            content: info.res + (info.status == 3 ? '接受' : '拒绝') + '了你的好友请求',
            type: 1,
            acceptText: '我知道了',
            id: info.id
          });
        } else if (info.type == 2) {
          arr.push({
            content: info.req + '请求添加你为好友',
            type: 0,
            acceptText: '同意',
            refuseText: '拒绝',
            id: info.id
          });
        } else if (info.type == 3) {
          arr.push({
            content: info.res + '接受了你的配对请求并向你发送了好友请求',
            type: 0,
            acceptText: '同意',
            refuseText: '拒绝',
            id: info.id,
            pid: info.pid
          });
        }
      }
      this.infoList = arr;
    }).catch(err => {
      console.log(err);
    });
  }
}
</script>

<style>
.homeTitle {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}

.homeTitle > .logo {
  width: 120px;
  height: 60px;
  background-image: url("~assets/img/logo.png");
  background-size: 100%;
  background-repeat: no-repeat;
  cursor: pointer;
}

.homeTitle > .seizeASeat {
  flex: 1;
}

.homeTitle > .search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 40px;
  padding: 0px 0px 0px 16px;
  border: 1px solid rgb(58, 175, 221);
  border-radius: 50px;
  box-sizing: border-box;
}

.homeTitle > .search > input {
  width: 240px;
  border: 0px;
  font-size: 16px;
  color: #666;
  background-color: transparent;
}

.homeTitle > .search > button {
  width: 38px;
  height: 38px;
  border: 0px;
  border-radius: 50px;
  background-image: url("~assets/img/search.png");
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center center;
  background-color: rgb(58, 175, 221);
  cursor: pointer;
  transition: background-color 0.3s;
}

.homeTitle > .search > button:hover {
  background-color: rgb(98, 198, 238);
}

.homeTitle > .information {
  position: relative;
  width: 38px;
  height: 38px;
  margin-left: 20px;
  border-radius: 50px;
  background-image: url("~assets/img/information.png");
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center center;
  background-color: rgb(58, 175, 221);
  cursor: pointer;
  transition: background-color 0.3s;
}

.homeTitle > .information:hover {
  background-color: rgb(98, 198, 238);
}

.homeTitle > .information > .infoExist {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 10px;
  height: 10px;
  border-radius: 50px;
  background-color: #f00;
}

.homeTitle > .baffles {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.homeTitle > .informationBox {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 100;
  width: 500px;
  height: 500px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px #fff;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.8);
  transform: translate(-50%, -50%);
}

.homeTitle > .informationBox > .title {
  font-size: 24px;
  text-align: center;
}

.homeTitle > .informationBox > .empty {
  font-size: 40px;
  color: #666;
  text-align: center;
  line-height: 400px;
}

.homeTitle > .informationBox > ul {
  height: 360px;
  padding: 0px 5px 0px 5px;
  margin-top: 20px;
  overflow-y: scroll;
}

.homeTitle > .informationBox > ul::-webkit-scrollbar {
  display: none;
}

.homeTitle > .informationBox > ul > li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 12px;
  box-shadow: 0px 3px 5px -2px #333;
  box-sizing: border-box;
  background-color: #fff;
  cursor: pointer;
  transition: background 0.3s;
}

.homeTitle > .informationBox > ul > li:hover {
  background-color: #eee;
}

.homeTitle > .informationBox > ul > li > .content {
  flex: 1;
  font-size: 16px;
}

.homeTitle > .informationBox > ul > li > button {
  padding: 2px 8px;
  border: 0px;
  border-radius: 5px;
  margin-left: 10px;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s;
}

.homeTitle > .informationBox > ul > li > .accept {
  background-color: #3fc079;
}

.homeTitle > .informationBox > ul > li > .accept:hover {
  background-color: #439b6a;
}

.homeTitle > .informationBox > ul > li > .refuse {
  background-color: #ff8656;
}

.homeTitle > .informationBox > ul > li > .refuse:hover {
  background-color: #f05418;
}

.homeTitle > .informationBox > button {
  position: absolute;
  left: 50%;
  bottom: 20px;
  padding: 5px 20px;
  border: 0px;
  border-radius: 5px;
  color: #fff;
  background-color: #3fc079;
  transform: translate(-50%, 0);
  cursor: pointer;
  transition: background-color 0.3s;
}

.homeTitle > .informationBox > button:hover {
  background-color: #439b6a;
}
</style>


