<template>
  <div class="frindListBox">
    <div class="title">好友列表</div>
    <div class="controller">
      <div class="search">
        <input type="text" v-model="searchText" @keydown.enter="search" />
        <button @click="searchClick"></button>
      </div>
      <div class="addFriend" @click="addFriend"></div>
      <div class="removeFriend" @click="removeFriend"></div>
    </div>
    <ul>
      <li v-for="friend in showList" @click="listClick(friend)">
        <div class="username">{{friend.username}}</div>
        <div class="sno">学号：{{friend.sno}}</div>
      </li>
    </ul>
    <home-user-info v-if="userInfoStatus" @submit="closeInfo" :sno="sno" :type="'show'"></home-user-info>
    <home-tips v-if="tipsStatus" :data="tipsData" @accept="tipsAccept" @refuse="tipsStatus = false"></home-tips>
  </div>
</template>

<script>
import { getFriendList, getUserList, addFriendRequest, removeFriend } from 'network/Home';
import HomeUserInfo from './HomeUserInfo.vue';
import HomeTips from './HomeTips.vue';

export default {
  name: 'HomeFriendList',
  data () {
    return {
      inputBox: null,
      friendList: [],
      showList: [],
      userInfoStatus: false,
      tipsStatus: false,
      sno: null,
      searchText: '',
      type: 'default',
      tipsData: {},
      nowUser: {}
    };
  },
  methods: {
    showUserInfo (sno) {
      this.sno = sno;
      this.userInfoStatus = true;
    },
    closeInfo () {
      this.userInfoStatus = false;
    },
    searchClick () {
      this.type = 'default';
      this.search();
    },
    search () {
      this.showList = [];
      for (let i = 0; i < this.friendList.length; i++) {
        if (this.friendList[i].username.indexOf(this.searchText) != -1) {
          this.showList.push(this.friendList[i]);
        } else if (this.friendList[i].sno == this.searchText) {
          this.showList.push(this.friendList[i]);
        }
      }
    },
    listClick (user) {
      if (this.type == 'default') {
        this.showUserInfo(user.sno);
      } else if (this.type == 'add') {
        this.nowUser = user;
        this.tipsData.content = '要添加' + user.username + '为好友吗';
        this.tipsData.acceptText = '添加';
        this.tipsData.refuseText = '取消';
        this.tipsStatus = true;
      } else if (this.type == 'remove') {
        this.nowUser = user;
        this.tipsData.content = '要删除好友' + user.username + '吗';
        this.tipsData.acceptText = '删除';
        this.tipsData.refuseText = '取消';
        this.tipsStatus = true;
      }
    },
    addFriend () {
      this.type = 'add';
      getUserList({
        message: this.searchText
      }).then(res => {
        this.showList = res.data.userList;
      }).catch(err => {
        console.log(err);
      });
    },
    removeFriend () {
      if (this.type != 'remove') {
        this.type = 'remove';
      } else {
        this.type = 'default';
      }
      this.search();
    },
    tipsAccept () {
      if (this.type == 'add') {
        addFriendRequest({
          applicant: this.$store.state.user.sno,
          requested: this.nowUser.sno
        }).then(res => {
          if (res.data.status) {
            alert('已发送好友请求');
          } else {
            console.log('addFriend false');
          }
        }).catch(err => {
          console.log(err);
        })
      } else if (this.type == 'remove') {
        removeFriend({
          reqSno: this.$store.state.user.sno,
          resSno: this.nowUser.sno
        }).then(res => {
          if (res.data.status) {
            let arr = [];
            for (let i = 0; i < this.friendList.length; i++) {
              if (this.friendList[i] == this.nowUser) {
                continue;
              }
              arr.push(this.friendList[i]);
            }
            this.friendList = arr;
            this.search();
            setTimeout(() => {
              alert('已删除该好友');
            }, 10);
          } else {
            console.log('removeFriend false');
          }
        }).catch(err => {
          console.log(err);
        });
      }
      this.tipsStatus = false;
    },
    updateFriendListEvent () {
      getFriendList({
        sno: this.$store.state.user.sno
      }).then(res => {
        this.friendList = res.data.friendList;
        if (this.type != 'add') {
          this.search();
        }
      }).catch(err => {
        console.log(err);
      });
    }
  },
  created () {
    getFriendList({
      sno: this.$store.state.user.sno
    }).then(res => {
      this.friendList = res.data.friendList;
      this.search();
    }).catch(err => {
      console.log(err);
    });
  },
  mounted () {
    this.inputBox = this.$el.querySelector('.controller > .search > input');
  },
  components: {
    HomeUserInfo,
    HomeTips
  }
}
</script>

<style>
.frindListBox {
  width: 100%;
  height: 100%;
  padding: 15px;
  box-sizing: border-box;
}

.frindListBox > .title {
  font-size: 20px;
}

.frindListBox > .controller {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 10px;
}

.frindListBox > .controller > .search {
  flex: 1;
  position: relative;
  height: 20px;
  padding: 0px 0px 0px 10px;
  border: 1px solid #3aafdd;
  border-radius: 50px;
  box-sizing: border-box;
}

.frindListBox > .controller > .search > input {
  display: block;
  width: 100px;
  height: 100%;
  border: 0px;
  font-size: 14px;
  background-color: transparent;
}

.frindListBox > .controller > .search > button {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 20px;
  height: 20px;
  border: 0px;
  border-radius: 50px;
  color: #fff;
  background-image: url("~assets/img/friendListSearch.png");
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center center;
  background-color: #3aafdd;
  cursor: pointer;
  transition: background-color 0.3s;
}

.frindListBox > .controller > .search > button:hover {
  background-color: #62c6ee;
}

.frindListBox > .controller > .addFriend,
.frindListBox > .controller > .removeFriend {
  width: 20px;
  height: 20px;
  margin-left: 8px;
  border-radius: 50px;
  color: #fff;
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center center;
  background-color: #3aafdd;
  cursor: pointer;
  transition: background-color 0.3s;
}

.frindListBox > .controller > .addFriend:hover,
.frindListBox > .controller > .removeFriend:hover {
  background-color: #62c6ee;
}

.frindListBox > .controller > .addFriend {
  background-image: url("~assets/img/friendListAdd.png");
}

.frindListBox > .controller > .removeFriend {
  background-image: url("~assets/img/friendListRemove.png");
}

.frindListBox > ul {
  height: 410px;
  padding: 10px 10px 0px 0px;
  box-sizing: border-box;
  overflow-y: scroll;
}

.frindListBox > ul::-webkit-scrollbar {
  width: 6px;
}

.frindListBox > ul::-webkit-scrollbar-thumb {
  background-color: #999;
  border-radius: 10px;
}

.frindListBox > ul:hover::-webkit-scrollbar-thumb {
  background-color: #666;
}

.frindListBox > ul > li {
  height: 45px;
  padding: 2px 0px 2px 20px;
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 1px 3px 5px -3px #666;
  box-sizing: border-box;
  background-color: #fff;
  cursor: pointer;
  transition: transform 0.3s, color 0.3s;
}

.frindListBox > ul > li:hover {
  background-color: #eee;
  transform: translate(0px, -3px);
}

.frindListBox > ul > li > .username {
  font-size: 18px;
  color: #333;
}

.frindListBox > ul > li > .sno {
  font-size: 8px;
  color: #666;
}
</style>


